<template>
    <section class="layout">
        <div class="form" v-if="!isAuthorized">
            <h1 class="title">Вход</h1>

            <div class="item">
                <MDInput label="Логин" ref="login" @input="onInput"></MDInput>
            </div>

            <div class="item">
                <MDInput label="Пароль" ref="password" @input="onInput"></MDInput>
            </div>

            <div class="item bottom-item">
                <button v-on:click="loginInit">Войти</button>
            </div>
        </div>

        <div class="form" v-if="isAuthorized">
            <h1 class="title">Вы уже вошли в аккаунт</h1>
        </div>
    </section>
</template>

<script>
    import { mapActions } from 'vuex';
    import modules from "../store/all.modules.name"
    import actions from "../store/all.actions.type";

    import MDInput from "./components/MD/MDInput";

    import jToken from "../common/jToken"

    export default {
        name: "Login",

        components: {MDInput},

        data() {
            return {
                isAuthorized: false,
                incorrectData: [],
            }
        },

        methods: {
            ...mapActions(modules.user, [
                actions.user.LOGIN,
                actions.user.LOGOUT,
            ]),

            onInput() {
                let res = this.incorrectData.find((item) => item.login === this.$refs.login.data
                    && item.password === this.$refs.password.data);

                if (res) {
                    this.$refs.login.isValid = false;
                    this.$refs.password.isValid = false;
                } else {
                    this.$refs.login.isValid = !this.$refs.login.isEmpty;
                    this.$refs.password.isValid = !this.$refs.password.isEmpty;
                }
            },

            loginInit() {
                if (this.$refs.login.isValid && this.$refs.password.isValid) {
                    let login = this.$refs.login.data;
                    let password = this.$refs.password.data;

                    this.login({login, password})
                        .then(isOk => {
                            if (isOk) {
                                this.$router.push({name: "home"})
                            } else {
                                this.incorrectData.push({
                                    login,
                                    password,
                                });
                                this.onInput();
                            }
                        })
                }
            },
        },

        mounted() {
            this.isAuthorized = jToken.has();

            if (jToken.has()) {
                this.$refs.login.isValid = false;
                this.$refs.password.isValid = false;
            }
        },
    }
</script>

<style lang="sass" scoped>
    @import '../assets/basic'

    .layout
        width: 90%
        max-width: 500px
        margin: auto
        font-family: Roboto, sans-serif

        .form
            box-shadow: 0 0 4px rgba(0, 0, 0, 0.5)
            border-radius: 1rem
            padding: 1rem

            .title
                text-align: center
                font-size: 1.5rem
                margin-top: 0

            .bottom-item
                display: flex
                justify-content: center

            .item
                margin-top: 3rem

                button
                    display: block
                    width: 100%
                    max-width: 150px
                    background-color: $secondary-color
                    color: $background-color
                    font-size: 16px
                    padding-top: 5px
                    padding-bottom: 5px
                    border: none
                    border-radius: 5px

                    &:hover
                        cursor: pointer

                    &:focus
                        outline: none
</style>